Nav {
    align-items: center;
    background: var(--theme-bg-color1);
    display: flex;
    height: 2.5rem;
    justify-content: space-between;
    left: 0;
    padding: 1vh 1vw;
    position: sticky;
    right: 0;
    top: 0;
    z-index: 1;
}


#App-title {
    color: #3f51b5;
    font-size: larger;
    font-weight: bolder;
}

#Navbar-rightside {
    display: flex;
    flex-flow: row;
    flex-grow: 4;
    gap: 1vw;
    height: 100%;
    justify-content: end;
    padding: 1vh 1vw;
    position: relative;
    width: 140px;
}

#profile-svg {
    background: url(data:image/jpeg;base64,);
    background-color: #9b9fbd;
    background-position: 50%;
    background-repeat: no-repeat;
    aspect-ratio: 1/1;
    border-radius: 50%;
    height: 100%;
}